import { CardContent, CardHeader, Container, Typography, Card, Paper, TableContainer, Table, TableRow, TableCell, TableHead, TableBody } from "@material-ui/core";
import { useEffect } from "react";
import { useState } from "react";
import TextField from "./TextField";
import axios from 'axios'

const HandicapList = () => {

    const [hcList, setHcList] = useState([])
    const [filter, setFilter] = useState('')

    useEffect(() => {
        axios.get((process.env.NODE_ENV !== 'production') ?
            'http://localhost:3001/api/hc' :
            'https://risbeegomfkerho.herokuapp.com/api/hc'
            , { headers: { authorization: 'bearer ' + window.localStorage.getItem('passuToken') } }).then((res) => {
                setHcList(res.data)
            })
    }, [])
    return (
        <Container>
            <Typography variant="h3">Handicapit</Typography>
            <TextField placeholder="Etsi rata" value={filter} onChange={(e) => setFilter(e.target.value)} size="small" />
            {hcList.filter(r => r.course.name.includes(filter))
                    .map(i => <Rata key={i.course.name + i.course.layout} rata={i} />)}
        </Container>
    )
}
const Rata = ({ rata }) => {
    return (
        <Card component={Paper} elevation={3} style={{ marginBottom: 10 }}>
            <CardHeader
                title={rata.course.name}
                subheader={rata.course.layout}
            />
            <CardContent>
                <TableContainer>
                    <Table size="small" className="taulu">
                        <TableHead>
                            <TableRow>
                            <TableCell>Nimi</TableCell>
                            <TableCell>HC</TableCell>
                            <TableCell>Pelit</TableCell>
                            <TableCell>Keskiarvo</TableCell>
                            <TableCell>Mediaani</TableCell>
                            <TableCell>Viim. 10 kierrosta</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                        {rata.players.sort((a,b) => b.games - a.games)
                            .map(p => <Pelaaja key={p.name + rata.course.name + rata.course.layout} pelaaja={p} />)}
                        </TableBody>
                    </Table>
                </TableContainer>
            </CardContent>
        </Card>
    )
}
const Pelaaja = ({ pelaaja }) => {
    const { name, games, average, median, lastRounds, hc } = pelaaja;
    return (
        <TableRow>
            <TableCell><b>{name}</b></TableCell>
            <TableCell>{hc}</TableCell>
            <TableCell>{games}</TableCell>
            <TableCell>{Math.round(average * 100) / 100}</TableCell>
            <TableCell>{median}</TableCell>
            <TableCell>{JSON.stringify(lastRounds).slice(1,-1).replace(/[,]/g, ', ')}</TableCell>
        </TableRow>
    )
}
export default HandicapList;
